.main {
    background-color: $bg-blue;

    margin-bottom: 47px;

    &--event {
        .main__menu {
            display: none;
        }

        .main__main-slider {
            min-height: 570px;
        }
    }
}

@media (min-width: 733px) and (max-width: 915px) {
    .main {
        &--event {
            .main__main-slider {
                min-height: 420px;
            }
        }
    }
}

.main__wrapper {
    width: 100%;
    max-width: 1423px;
    margin: 0 auto;
    padding-left: 39px;
    padding-right: 40px;
    padding-top: 35px;
}

.main__menu {
    margin-bottom: 38px;
}

.main__main-slider,
.main__slider:not(:last-child) {
    margin-bottom: 33px;
}

